.skillsBlock {
  .skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    &::before {
      content: '';
      position: absolute;
      background: #dce1e4;
      width: 100%;
      height: 1px;
    }

    .skills {
      width: 100%;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
  }
}
